<template>
  <v-row justify="center">
    <v-dialog
      v-model="dialog"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          fab
          dark
          color="rgba(39,55,70,1)"
          fixed
          right
          bottom
          v-bind="attrs"
          v-on="on"
          @click="newForm()"
        >
          <v-icon size="23" color="#D5DBDB" dark>add</v-icon>
        </v-btn>
      </template>
      <v-card color="#F2F4F4">
        <v-card class="overflow-hidden">
          <v-app-bar
            style="z-index: 20001 !important"
            absolute
            dark
            color="primary"
            elevate-on-scroll
            scroll-target="#scrolling-techniques-7"
          >
            <v-btn icon dark @click="dialog = false">
              <v-icon>arrow_back</v-icon>
            </v-btn>
            <v-toolbar-title>{{ textDialog }}</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
              <v-btn dark text @click="action()"> {{ textSave }} </v-btn>
            </v-toolbar-items>
          </v-app-bar>
          <v-sheet
            id="scrolling-techniques-7"
            class="overflow-y-auto"
            height="100vh"
            style="color: #273746"
          >
            <v-container>
              <v-card-title class="text-h5" style="color: #273746">
                Nueva Area
              </v-card-title>
              <v-card-text style="color: #273746">Datos del Area</v-card-text>

              <v-form ref="form">
                <v-text-field
                  class="text-capitalize"
                  outlined
                  disabled
                  v-model="reference"
                  label="REFERENCIA"
                  required
                ></v-text-field>
                <v-text-field
                  outlined
                  v-model="nomenclature"
                  label="NOMENCLATURA"
                  required
                ></v-text-field>
                <v-text-field
                  outlined
                  v-model="location"
                  label="LOCALIZACIÓN"
                  required
                ></v-text-field>

                <!-- Inicia el pendiente... -->
                <v-text-field
                  outlined
                  v-model="capacity"
                  label="CAPACIDAD [KVA]"
                  required
                ></v-text-field>

                <v-select
                  outlined
                  :items="typeItems"
                  v-model="type"
                  label="TIPO"
                ></v-select>

                <v-text-field
                  outlined
                  v-model="brand"
                  label="Marca"
                  required
                ></v-text-field>

                <v-text-field
                  outlined
                  v-model="maximumRecordedTemperature"
                  label="TEMPERATURA MÁXIMA REGISTRADA EN LÍQUIDO AISLANTE [⁰C]"
                  required
                ></v-text-field>

                <v-text-field
                  outlined
                  v-model="currentTemperature"
                  label="TEMPERATURA ACTUAL EN LÍQUIDO AISLANTE [⁰C]"
                  required
                ></v-text-field>

                <v-text-field
                  outlined
                  v-model="temperatureRise"
                  label="ELEVACIÓN DE TEMPERATURA (PLACA DE DATOS)  [⁰C]"
                  required
                ></v-text-field>

                <v-select
                  outlined
                  :items="phasesWiresItems"
                  v-model="phasesWires"
                  label="FASES / HILOS"
                ></v-select>

                <v-select
                  outlined
                  :items="frequencyItems"
                  v-model="frequency"
                  label="FRECUENCIA"
                ></v-select>

                <v-text-field
                  outlined
                  v-model="primaryVoltage"
                  label="TENSIÓN PRIMARIA [V]"
                  required
                ></v-text-field>

                <v-text-field
                  outlined
                  v-model="secondaryVoltage"
                  label="TENSIÓN SECUNDARIA [V]"
                  required
                ></v-text-field>

                <v-select
                  outlined
                  :items="connectionItems"
                  v-model="connection"
                  label="CONEXIÓN"
                ></v-select>

                <v-select
                  outlined
                  :items="insulationClassItems"
                  v-model="insulationClass"
                  label="CLASE DE AISLAMIENTO"
                ></v-select>

                <!-- Aqui termina el pendiente.. -->

                <v-select
                  outlined
                  :items="oilLevelItems"
                  v-model="oilLevel"
                  label="NIVEL DE ACEITE"
                ></v-select>

                <v-text-field
                  outlined
                  v-model="lastMaintenanceDate"
                  label="ÚLTIMA FECHA DE MANTENIMIENTO"
                  required
                ></v-text-field>

                <item-group :config="result" v-if="dialog" />
              </v-form>
            </v-container>
          </v-sheet>
        </v-card>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import { mapState, mapActions } from "vuex";
import ItemGroup from "@/components/item/ItemGroup.vue";
export default {
  components: { ItemGroup },
  name: "TransformersNew",
  data() {
    return {
      editDialog: false,
      value: null,
      reference: 1,
      nomenclature: "",
      location: "",
      oilLevel: null,
      oilLevelItems: ["ALTO", "MEDIO", "BAJO"],
      dialog: false,
      nameEdit: "transformersList",
      //Nuevos parametros..
      capacity: "",
      type: "",
      typeItems: [
        "PEDESTAL",
        "DISTRIBUCIÓN",
        "SUBESTACIÓN ABIERTA",
        "GARGANTAS",
        "POSTE",
      ],
      brand: "",
      /*  brandItems: [
        "ABB",
        "SQUARE D",
        "SIMENES",
        "BTICINO",
        "SCHNEIDER ELECTRIC",
        "FEDERAL PACIFIC",
      ], */
      maximumRecordedTemperature: "",
      currentTemperature: "",
      temperatureRise: "",
      phasesWires: "",
      phasesWiresItems: ["1F-3H", "3F-4H"],
      frequency: "",
      frequencyItems: ["60 Hz", "50 Hz"],
      primaryVoltage: "",
      secondaryVoltage: "",
      connection: "",
      connectionItems: ["DELTA - ESTRELLA", "ESTRELLA - ESTRELLA"],
      insulationClass: "",
      insulationClassItems: ["15 kV", "34 kV"],
      lastMaintenanceDate: "",
    };
  },
  computed: {
    ...mapState({
      data: (state) => state.customers.findCustomer,
    }),
    items() {
      return this.data[this.nameEdit].data;
    },
    config() {
      return this.data[this.nameEdit].config;
    },
    textDialog() {
      if (this.editDialog) return "Editar Transformador";
      return "Nuevo Transformador";
    },
    textSave() {
      if (this.editDialog) return "Guardar";
      return "Crear";
    },
  },
  methods: {
    ...mapActions({
      getItem: "customers/search",
      saveItem: "customers/save",
    }),
    //Funciones de soporte..
    action() {
      if (this.editDialog) {
        this.update();
      } else {
        this.create();
      }
    },
    //asignar datos...
    toAssign(data) {
      //Asignar para poder visualizar datos guardados.
      this.reference = data.reference;
      this.nomenclature = data.nomenclature;
      this.location = data.location;
      this.oilLevel = data.oilLevel;
      //News
      this.capacity = data?.capacity;
      this.type = data?.type;
      this.brand = data?.brand;
      this.maximumRecordedTemperature = data?.maximumRecordedTemperature;
      this.currentTemperature = data?.currentTemperature;
      this.temperatureRise = data?.temperatureRise;
      this.phasesWires = data?.phasesWires;
      this.frequency = data?.frequency;
      this.primaryVoltage = data?.primaryVoltage;
      this.secondaryVoltage = data?.secondaryVoltage;
      this.connection = data?.connection;
      this.insulationClass = data?.insulationClass;
      this.lastMaintenanceDate = data?.lastMaintenanceDate;
    },
    //Vaciar variables..
    emptyData() {
      this.nomenclature = "";
      this.location = "";
      this.oilLevel = null;
      this.value = null;
      this.capacity = null;
      this.type = null;
      this.brand = null;
      this.maximumRecordedTemperature = null;
      this.currentTemperature = null;
      this.temperatureRise = null;
      this.phasesWires = null;
      this.frequency = null;
      this.primaryVoltage = null;
      this.secondaryVoltage = null;
      this.connection = null;
      this.insulationClass = null;
      this.lastMaintenanceDate = null;
    },
    //Configuración para abrir o crear nuevo formulario..
    newForm() {
      console.log("hola");
      this.emptyData();
      this.editDialog = false;
      this.reference = this.items.length + 1;
      this.result = JSON.parse(JSON.stringify(this.config));
    },
    openForm(data) {
      console.log("se abre..");
      this.editDialog = true;
      this.dialog = true;
      console.log(data);
      this.toAssign(data);
      this.result = data.result;
    },
    //Acciones de Actualizar y Crear...
    update() {
      const newDataUpdate = this.data[this.nameEdit].data.map((a) =>
        a.reference == this.reference
          ? {
              ...a,
              nomenclature: this.nomenclature,
              location: this.location,
              oilLevel: this.oilLevel,
              result: this.result,
              //News
              capacity: this.capacity,
              type: this.type,
              brand: this.brand,
              maximumRecordedTemperature: this.maximumRecordedTemperature,
              currentTemperature: this.currentTemperature,
              temperatureRise: this.temperatureRise,
              phasesWires: this.phasesWires,
              frequency: this.frequency,
              primaryVoltage: this.primaryVoltage,
              secondaryVoltage: this.secondaryVoltage,
              connection: this.connection,
              insulationClass: this.insulationClass,
              lastMaintenanceDate: this.lastMaintenanceDate,
            }
          : a
      );
      this.data[this.nameEdit].data = newDataUpdate;
      this.saveItem(this.data);
      this.dialog = false;
      this.emptyData();
    },
    create() {
      const dt = {
        reference: this.reference,
        location: this.location,
        result: this.result,
        oilLevel: this.oilLevel,
        nomenclature: this.nomenclature,
        //news
        capacity: this.capacity,
        type: this.type,
        brand: this.brand,
        maximumRecordedTemperature: this.maximumRecordedTemperature,
        currentTemperature: this.currentTemperature,
        temperatureRise: this.temperatureRise,
        phasesWires: this.phasesWires,
        frequency: this.frequency,
        primaryVoltage: this.primaryVoltage,
        secondaryVoltage: this.secondaryVoltage,
        connection: this.connection,
        insulationClass: this.insulationClass,
        lastMaintenanceDate: this.lastMaintenanceDate,
      };
      this.data[this.nameEdit].data.push(dt);
      this.saveItem(this.data);
      this.emptyData();
      this.dialog = false;
      //console.log(dt);
    },
  },
};
</script>

<style scoped>
.code input {
  text-transform: uppercase;
}
</style>